<template>
    <div>
        <b-navbar toggleable="lg" type="dark" variant="info">
            <b-navbar-brand href="#">
                <img src="../assets/bitcoin.png" class="rounded float-left" width="25" height="25" alt="">
            </b-navbar-brand>

            <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

            <b-collapse id="nav-collapse" is-nav>
                <b-navbar-nav>
                    <b-nav-item to="/">{{ $t('nav.home') }}</b-nav-item>

                    <b-nav-item to="/about">{{ $t('nav.documentation') }}</b-nav-item>
                </b-navbar-nav>

                <!-- Right aligned nav items -->
                <b-navbar-nav class="ml-auto">
                    <b-nav-item-dropdown :text="$t('nav.languages')" right>
                        <b-dropdown-item href="#" @click="ChangeLanguage('en')">ENGLISH</b-dropdown-item>
                        <b-dropdown-item href="#" @click="ChangeLanguage('zh')">简体中文</b-dropdown-item>
                    </b-nav-item-dropdown>

                    <b-nav-item-dropdown v-show="IsLogin" right>
                        <!-- Using 'button-content' slot -->
                        <template v-slot:button-content >
                            <em>{{ $t('nav.user') }}</em>
                        </template>
                        <b-dropdown-item href="#">{{ $t('nav.profile') }}</b-dropdown-item>
                        <b-dropdown-item href="#">{{ $t('nav.signOut') }}</b-dropdown-item>
                    </b-nav-item-dropdown>

                    <b-navbar-nav v-show="!IsLogin">
                        <b-nav-item to="/login">{{ $t('nav.login') }}</b-nav-item>

                        <b-nav-item to="/register">{{ $t('nav.register') }}</b-nav-item>
                    </b-navbar-nav>

                </b-navbar-nav>
            </b-collapse>
        </b-navbar>
    </div>
</template>

<script>
	export default {
		name: "AppNav",
        computed: {
			IsLogin(){
				return  this.$store.state.IsLogin
            }
        },
        methods:{
			ChangeLanguage: function(lang){
				if(lang=='zh' || lang== 'en'){
					this.$root.$i18n.locale=lang
                }
            }
        }
	}
</script>

