<template>
    <div>
        <b-form @reset.prevent="onReset" @submit.prevent="onSubmit" v-if="show">

            <b-form-group
                    id="input-username-group"
                    label="Your Name:"
                    label-for="input-username"
                    :state="usernameValid"
                    invalid-feedback="用户名长度大于4的字母和数字组合且必须以字母开头"
                    valid-feedback="用户名合法"
            >
                <b-form-input
                        id="input-username"
                        placeholder="Enter name"
                        required
                        v-model="form.username"
                        autocomplete
                        :state="usernameValid">
                </b-form-input>
            </b-form-group>

            <b-form-group
                    id="input-email-group"
                    label="Your Email:"
                    label-for="input-email"
                    invalid-feedback="邮件格式不正确"
                    valid-feedback="email is Ok"
            >
                <b-form-input
                        id="input-email"
                        placeholder="Enter email"
                        required
                        type="email"
                        v-model="form.email"
                        autocomplete
                        :state="emailValid"
                        >
                </b-form-input>
            </b-form-group>

            <b-form-group
                    id="input-password-group"
                    label="Your Password:"
                    label-for="input-Password"
                    invalid-feedback="密码不能少于8位,且至少包含一个数字和一个字母"
                    valid-feedback="密码强度可以"
            >
                <b-form-input
                        id="input-Password"
                        placeholder="Enter Password"
                        required
                        v-model="form.password"
                        autocomplete
                        :state="pwdValid"
                >
                </b-form-input>
            </b-form-group>


            <b-button type="submit" variant="primary">Submit</b-button>
            <b-button type="reset" variant="danger">Reset</b-button>
        </b-form>
    </div>
</template>

<script>
	export default {
		data() {
			return {
				form: {
					username: '',
					email: '',
					password: '',
					password2: '',
					captcha:''
				},
				show: true
			}
		},
		methods: {
			onSubmit() {
				console.log(JSON.stringify(this.form))
			},
			onReset() {
				[this.form.username,this.form.email,this.form.password,this.form.password2,this.form.captcha] = ['','','','','']
				this.show = false
				this.$nextTick(() => {
					this.show = true
				})
			},
		},
        computed: {
	        usernameValid() {
	        	//用户名长度大于4并且只能是字符和数字，只能以字母开头
                console.log(this.form.username)
	        	return this.form.username.length>4 ? true : false
	        },
	        emailValid(){
	        	let emailReg="(?:[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*|\"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\\\[\x01-\x09\x0b\x0c\x0e-\x7f])*\")@(?:(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?|\\[(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?|[a-z0-9-]*[a-z0-9]:(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21-\x5a\x53-\x7f]|\\\\[\x01-\x09\x0b\x0c\x0e-\x7f])+)\\])"
		        let re = new RegExp(emailReg)
                return re.test(this.form.email)
            },
	        pwdValid(){
	        	console.log(this.form.password)
	        	let pwdReg = /^(?=.*[A-Za-z])(?=.*\d)[^ -~]{8,}$/
                let re = new RegExp(pwdReg)
                let mat = this.form.password.match(pwdReg)
                console.log(mat)
                let result = re.test(this.form.password)
                console.log(result)
                return result
            }

        }
	}
</script>